import React from "react";

const SelectorOption = ({ value, func, selected }) => {
  return (
    <div
      style={{
        flexGrow: 1,
        background: value === selected ? "#ff0000" : "#aa0000",
        textAlign: "center"
      }}
      onClick={func}
    >
      {value}
    </div>
  );
};

export default SelectorOption;
